/* --- T·ªîNG TH·ªÇ TRANG --- */
.visual-page {
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 40px 0;
  background: #f0f4f8; /* N·ªÅn x√°m xanh c·ª±c nh·∫°t, sang tr·ªçng */
