    radial-gradient(at 0% 0%, rgba(99, 102, 241, 0.15) 0, transparent 50%), 
    radial-gradient(at 50% 0%, rgba(168, 85, 247, 0.1) 0, transparent 50%);
  font-family: 'Inter', system-ui, sans-serif;
  overflow-x: hidden;
}

.content-container {
  position: relative;
  z-index: 1;
  width: 90%;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

/* --- TI√äU ƒê·ªÄ --- */
.algo-display-title {
