  font-size: 2.5rem;
  background: linear-gradient(90deg, #4f46e5, #9333ea);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  margin-bottom: 10px;
  letter-spacing: -1px;
}
.code-container {
  display: flex;
  flex-direction: column;
  background: #020617; /* V√πng code t·ªëi h∆°n ƒë·ªÉ n·ªïi b·∫≠t */
  padding: 15px;
  border-radius: 8px;
  overflow-x: auto; /* Cu·ªôn ngang n·∫øu code d√†i */
}
/* --- THANH ƒêI·ªÄU KHI·ªÇN (TOOLBAR) --- */
.toolbar-card {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border-radius: var(--radius-md);
  padding: 25px;
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-sm);
  margin-bottom: 30px;
}
/* C√°c c·ªôt d·ªØ li·ªáu */
.bar {
  background: linear-gradient(180deg, var(--color-primary) 0%, #9857d5 100%);
  border-radius: 4px 4px 0 0;
  transition: height 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.styled-input {
  border: 2px solid #e2e8f0;
  padding: 12px 18px;
  border-radius: 12px;
  width: 320px;
  font-weight: 500;
  transition: all 0.3s;
}
.code-line {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  padding: 4px 8px;
  border-radius: 4px;
  transition: background 0.2s ease;
}
.code-row {
  display: grid;
  grid-template-columns: 30px 1fr; /* C·ªë ƒë·ªãnh c·ªôt s·ªë d√≤ng 30px */
  gap: 15px;
  align-items: flex-start;
  padding: 4px 0;
}
.styled-input:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.1);
  outline: none;
}
/* Container ch·ª©a n√∫t ƒë·ªÉ ƒë·∫£m b·∫£o kho·∫£ng c√°ch v√† cƒÉn h√†ng */
.button-group {
  display: flex;
  gap: 12px;
  align-items: center;
}
.line-number {
  color: #64748b;
  text-align: right;
  font-family: 'Fira Code', monospace;
  font-size: 0.85rem;
  border-right: 1px solid #1e293b;
  padding-right: 12px;
}
/* --- PHONG C√ÅCH N√öT B·∫§M HI·ªÜN ƒê·∫†I --- */
.btn-init, .btn-run {
  padding: 10px 24px;
  border-radius: 12px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1); /* Hi·ªáu ·ª©ng n·∫£y nh·∫π */
  border: none;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
}

/* N√∫t Kh·ªüi t·∫°o: Thanh l·ªãch, m√†u x√°m xanh trung t√≠nh */
.btn-init {
  background: #ffffff;
  color: #4f46e5;
  border: 2px solid #e2e8f0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.btn-init:hover {
  border-color: #4f46e5;
  background: #f8faff;
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(79, 70, 229, 0.1);
}

/* N√∫t Ch·∫°y: M√†u xanh l√° c√¢y ƒë·∫ßy nƒÉng l∆∞·ª£ng (Gradient) */
.btn-run {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.btn-run:hover:not(:disabled) {
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4);
}

/* Tr·∫°ng th√°i khi ƒëang ch·∫°y (T·∫°m d·ª´ng): Chuy·ªÉn sang m√†u ƒë·ªè c·∫£nh b√°o */
.btn-run.playing {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

.btn-run.playing:hover {
  box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4);
}

/* Hi·ªáu ·ª©ng khi click chu·ªôt */
.btn-init:active, .btn-run:active {
  transform: scale(0.95);
}
/* --- KHUNG BI·ªÇU ƒê·ªí (VISUAL BOX) --- */
.visual-box {
  display: flex;
  align-items: flex-end; /* Quan tr·ªçng: G·ªëc t·ªça ƒë·ªô ·ªü ƒë√°y */
  justify-content: center;
  height: 400px; /* Chi·ªÅu cao c·ªë ƒë·ªãnh ƒë·ªÉ c√°c c·ªôt so s√°nh chu·∫©n */
  padding: 40px 20px 0; /* padding-top ƒë·ªÉ ch·ª´a ch·ªó cho s·ªë tr√™n ƒë·∫ßu c·ªôt */
  background: #ffffff;
  border-radius: 20px;
  box-shadow: inset 0 2px 10px rgba(0,0,0,0.02);
  gap: 8px; /* Kho·∫£ng c√°ch gi·ªØa c√°c c·ªôt */
  border: 1px solid #f1f5f9;
}
.code-text {
