  line-height: 1.6;
  white-space: pre; /* Quan tr·ªçng nh·∫•t ƒë·ªÉ gi·ªØ th·ª•t l·ªÅ nh∆∞ h√¨nh 3 */
}
/* --- B·∫¢NG ƒê·ªò PH·ª®C T·∫†P (COMPLEXITY CARD) --- */
.complexity-card-styled {
