/* --- N√öT RACE MODE SI√äU C·∫§P --- */
.btn-toggle-mode {
  position: relative;
  padding: 12px 28px;
  font-size: 1rem;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #fff;
  background: linear-gradient(45deg, #ff0055, #7000ff);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s ease;
  box-shadow: 0 0 15px rgba(112, 0, 255, 0.4);
  display: flex;
  align-items: center;
  gap: 10px;
  z-index: 10;
}

/* Hi·ªáu ·ª©ng tia ch·ªõp ch·∫°y qua n√∫t */
.btn-toggle-mode::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(255, 255, 255, 0.4),
    transparent
  );
  transition: 0.6s;
}

.btn-toggle-mode:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 0 25px rgba(255, 0, 85, 0.6);
}

.btn-toggle-mode:hover::before {
  left: 100%;
}

/* Hi·ªáu ·ª©ng nh·∫•p nh√°y khi ·ªü ch·∫ø ƒë·ªô Race */
.btn-toggle-mode.active {
  background: linear-gradient(45deg, #22c55e, #10b981);
  box-shadow: 0 0 20px rgba(16, 185, 129, 0.5);
  animation: pulse-glow 2s infinite;
}

@keyframes pulse-glow {
  0% { box-shadow: 0 0 10px rgba(16, 185, 129, 0.5); }
  50% { box-shadow: 0 0 25px rgba(16, 185, 129, 0.8); }
  100% { box-shadow: 0 0 10px rgba(16, 185, 129, 0.5); }
}

/* Bi·ªÉu t∆∞·ª£ng l√° c·ªù chuy·ªÉn ƒë·ªông */
.race-icon {
  font-size: 1.4rem;
  animation: wave 1s infinite alternate ease-in-out;
}

@keyframes wave {
  from { transform: rotate(-10deg); }
  to { transform: rotate(10deg); }
}
