/* ===== 1. KHUNG CH√çNH (NAVY GRADIENT) ===== */
.about-wrapper {
  max-width: 1000px;
  margin: 40px auto;
  padding: 60px 30px;
  /* K·∫øt h·ª£p Gradient t·ªëi sang tr·ªçng */
  background: linear-gradient(145deg, #2b3348 0%, #1a202f 100%);
  border-radius: 32px;
  color: white;
  position: relative;
  overflow: hidden;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.4);
}

/* H·ªça ti·∫øt m√°y t√≠nh ch√¨m ngh·ªá thu·∫≠t */
.about-wrapper::before {
  content: "üíª";
  position: absolute;
  top: -20px;
  right: -30px;
  font-size: 25rem;
  opacity: 0.03;
  filter: grayscale(1) blur(2px);
  z-index: 0;
  transform: rotate(-15deg);
  pointer-events: none;
}

/* H·ªça ti·∫øt l∆∞·ªõi ch·∫•m c√¥ng ngh·ªá */
.about-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(#4f46e5 1px, transparent 1px);
  background-size: 30px 30px;
  opacity: 0.04;
  z-index: 0;
  pointer-events: none;
}

/* ===== 2. PH·∫¶N TI√äU ƒê·ªÄ (HERO) ===== */
.about-hero {
  text-align: center;
  margin-bottom: 50px;
  position: relative;
  z-index: 1;
}

.about-label {
  background: #ffffff;
  color: #4f46e5;
  padding: 6px 14px;
  border-radius: 100px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
}

.about-hero h1 {
  font-size: 3rem;
  margin-top: 20px;
  color: #f8faff;
  letter-spacing: -1px;
  font-weight: 800;
}

.about-hero p {
  font-size: 1.25rem;
  color: #e2e8f0;
  max-width: 600px;
  margin: 20px auto 0;
  line-height: 1.6;
}

/* ===== 3. GRID T·∫¶M NH√åN & QUY ∆Ø·ªöC ===== */
.about-grid-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 60px;
  position: relative;
  z-index: 1;
}

.about-card-large { 
  background: white; 
  padding: 35px; 
  border-radius: 24px; 
  color: #1e293b; 
  box-shadow: 0 15px 30px rgba(0,0,0,0.2);
  transition: transform 0.3s ease;
}

.about-card-large:hover {
  transform: translateY(-8px);
}

.about-card-large h2 {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 15px;
  color: #0f172a;
}

.about-card-large p {
  font-size: 1.05rem;
  line-height: 1.7;
  color: #475569;
}

/* Bi·ªÉu t∆∞·ª£ng trong card */
.card-icon { 
  font-size: 2.5rem; 
  margin-bottom: 15px;
  display: block;
}
.card-icon.blue { color: #3b82f6; } 
.card-icon.yellow { color: #eab308; }

/* ===== 4. PH·∫¶N ƒê·ªòI NG≈® (TEAM) ===== */
.team-section {
  margin-top: 50px;
  padding: 40px 20px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
}

.team-title {
  font-size: 1.75rem;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

.team-grid-layout {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 th·∫ª m·ªói h√†ng */
  gap: 30px;
  max-width: 1050px;
  margin: 0 auto;
 
}

.team-row-center {
  grid-column: 1 / -1;
  display: flex;
  justify-content: center;
  gap: 30px;
}




.member-mini-card {
  background: #ffffff;
  padding: 35px 20px;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}
.team-row-center .member-mini-card {
  flex: 1;
  max-width: 280px;
}


.member-mini-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(59, 130, 246, 0.2); /* ƒê√£ th√™m m√†u v√† ƒë√≥ng ngo·∫∑c ) */
}
.member-logo-wrapper {
  width: 60px; 
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #3b82f6;
  margin-bottom: 12px;
}

.member-logo-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mini-name {
  font-weight: 700;
  font-size: 0.9rem;
  color: #1e293b;
  display: block;
  margin-bottom: 5px;
  text-align: center;
}

.mini-role {
  font-size: 0.75rem;
  color: #64748b;
  margin-top: 4px;
  font-weight: 500;
  display: block; /* √âp vai tr√≤ xu·ªëng d√≤ng d∆∞·ªõi t√™n */
  text-align: center;
}

/* ===== 5. TR·∫†NG TH√ÅI (STATUS/DOTS) ===== */
.status-item { 
  display: flex; 
  align-items: center; 
  margin-bottom: 12px; 
  font-size: 0.95rem; 
  color: #334155; 
}

.dot { 
  width: 10px; 
  height: 10px; 
  border-radius: 50%; 
  display: inline-block; 
  margin-right: 12px; 
}
.dot.blue { background: #3b82f6; } 
.dot.red { background: #ef4444; } 
.dot.green { background: #10b981; }
@media (max-width: 900px) {
  .team-grid-layout {
    grid-template-columns: repeat(2, 1fr); /* Tablet */
  }
}

@media (max-width: 600px) {
  .team-grid-layout {
    grid-template-columns: 1fr; /* Mobile */
  }
}
