/* --- T·ªîNG TH·ªÇ HEADER HI·ªÜN ƒê·∫†I --- */
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 6%;
  height: 80px;
  background: rgba(255, 255, 255, 0.85); /* N·ªÅn k√≠nh m·ªù */
  backdrop-filter: blur(15px);
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.03);
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

/* --- LOGO & CH·ªÆ --- */
.logo-wrapper {
  display: flex;
  align-items: center;
  gap: 12px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.logo-wrapper:hover {
  transform: scale(1.03);
}

.nav-logo {
  height: 45px; /* K√≠ch th∆∞·ªõc logo ·∫£nh v·ª´a v·∫∑n */
  width: auto;
  filter: drop-shadow(0 2px 5px rgba(0,0,0,0.1));
}

.logo-text {
  font-size: 1.6rem;
  font-weight: 900;
  background: linear-gradient(135deg, #4f46e5, #9333ea);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.5px;
}

/* --- H·ªÜ TH·ªêNG MENU --- */
.nav-links {
  display: flex;
  gap: 35px;
  list-style: none;
  align-items: center;
}

.nav-links a, .dropbtn {
  text-decoration: none;
  font-weight: 600;
  color: #475569;
  font-size: 1rem;
  transition: all 0.3s ease;
  padding: 8px 0;
  position: relative;
}

/* Hi·ªáu ·ª©ng g·∫°ch ch√¢n ch·∫°y d∆∞·ªõi c√°c link */
.nav-links a::after, .dropbtn::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  background: linear-gradient(90deg, #4f46e5, #9333ea);
  transition: width 0.3s ease;
}

.nav-links a:hover, .nav-links a.active, .dropdown:hover .dropbtn {
  color: #4f46e5;
}

.nav-links a:hover::after, .nav-links a.active::after, .dropdown:hover .dropbtn::after {
  width: 100%;
}

/* --- DROPDOWN T√ôY CH·ªàNH --- */
.dropdown {
  position: relative;
}

.dropbtn {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(10px);
  background: white;
  min-width: 180px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 10px 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.dropdown:hover .dropdown-menu {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(0);
}

.dropdown-menu a {
  display: block;
  padding: 12px 20px;
  color: #1e293b;
  font-size: 0.9rem;
  transition: background 0.2s;
}

.dropdown-menu a::after { display: none; } /* T·∫Øt g·∫°ch ch√¢n trong menu con */

.dropdown-menu a:hover {
  background: #f1f5f9;
  color: #4f46e5;
}
/* Container b√™n ph·∫£i ch·ª©a menu v√† n√∫t ng√¥n ng·ªØ */
.navbar-right {
  display: flex;
  align-items: center;
  gap: 25px;
}

/* Style cho b·ªô chuy·ªÉn ng√¥n ng·ªØ */
.lang-switcher {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  padding: 4px 12px;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-left: 10px;
}

.lang-switcher button {
  background: none;
  border: none;
  color: #666;
  font-weight: 600;
  cursor: pointer;
  padding: 2px 6px;
  transition: all 0.3s ease;
  font-size: 14px;
}

/* Hi·ªáu ·ª©ng khi n√∫t ƒë∆∞·ª£c ch·ªçn */
.lang-switcher button.active {
  color: #7d5fff; /* M√†u t√≠m ch·ªß ƒë·∫°o c·ªßa b·∫°n */
  text-decoration: underline;
  text-underline-offset: 4px;
}

.lang-switcher button:hover {
  color: #7d5fff;
}

.lang-switcher .divider {
  color: #ccc;
  margin: 0 4px;
  font-size: 12px;
}